@charset "UTF-8";
.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  position: relative;
  min-height: 500px;
}

.innerWrap {
  text-align: center;
}

.figHeader {
  min-height: 80px;
  max-width: 800px;
  margin: 50px auto 0 auto;
}

.issueBlock {
  border: 2px solid;
  margin: 0 5px;
  font-size: 20px;
  display: inline-block;
}

@media all and (min-width: 800px) {
  .issueBlock {
    float: left;
    margin-top: 10px;
  }
  .issueController {
    float: right;
  }
}

.issueTitle {
  background: black;
  color: white;
  padding: 10px;
}

.issueStatement {
  background: white;
  padding: 10px;
}

@media all and (min-width: 440px) {
  .issueTitle {
    display: inline-block;
  }
  .issueStatement {
    display: inline-block;
  }
}

.records {
  margin: 60px auto;
  max-width: 1000px;
  position: relative;
  text-align: center;
  padding-bottom: 100px;
}

@media all and (min-width: 800px) {
  .mobilePadOnly {
    display: none;
  }
}

.collaboratorInfo {
  text-align: center;
  margin: 0 -9%;
  background: #d3d3d3;
}

.link, .link:visited {
  color: #b88234;
  text-decoration: none;
  display: inline-block;
  padding: 10px;
}

.link:hover {
  color: #b88234;
  text-decoration: underline;
}

.moreInfo {
  background: white;
  line-height: 2;
  text-align: left;
  padding-bottom: 50px;
}

.moreInfoInner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 10px;
  font-size: 22px;
  color: #353736;
}

.linkButton, .linkButton:visited {
  color: #D6A051;
  border: 2px solid #D6A051;
  padding: 0px 10px;
  font-size: 18px;
  margin: 0 10px 0px 10px;
  display: inline-block;
  text-decoration: none;
}

.linkButton:hover {
  background: #D6A051;
  color: white;
}

.comingButton {
  color: #D6A051;
  border: 2px dotted #D6A051;
  padding: 0px 10px;
  font-size: 18px;
  margin: 0 10px 0px 10px;
  display: inline-block;
  text-decoration: none;
}

.actionButtons {
  font-size: 24px;
  position: relative;
  text-align: center;
  max-width: 420px;
  margin: 20px auto 30px auto;
}

.actionButton {
  display: inline-block;
  font-size: 24px;
  position: relative;
  margin: 0 4px;
  padding: 10px 20px;
  background: #005060;
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.25);
  color: #f5f5f5;
  border-radius: 0;
  cursor: pointer;
}

.actionButton:hover {
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.5);
}

.backStage {
  cursor: pointer;
  text-align: left;
  max-width: 480px;
  margin: 0 auto 4px auto;
  color: #6D6868;
  padding-left: 4px;
  min-height: 30px;
  display: none;
}

.backStage:hover {
  text-decoration: underline;
}

.mobileHint {
  position: relative;
}

@media all and (min-width: 800px) {
  .mobileHint {
    display: none;
  }
}

.keyboardHint {
  display: none;
}

@media all and (min-width: 800px) {
  .keyboardHint {
    display: block;
    margin: 20px 10px 100px 10px;
    color: #505050;
  }
}

.skipInteractive {
  margin: 20px;
  cursor: pointer;
}

@media all and (min-width: 800px) {
  .skipInteractive {
    display: inline-block;
    margin: 0;
  }
}

/* 協力 NGO*/
.collaboratorInfo {
  text-align: center;
  padding: 10px 0;
  background: #ddd;
}

.completeMessage {
  text-align: left;
  max-width: 600px;
  margin: 100px auto 20px auto;
  padding: 30px;
  position: relative;
  font-size: 18px;
  text-align: center;
  line-height: 1.4;
}



