@import '../../common.scss';
.wrap {
	position: relative;
	text-align: center;
}

// 因為Party.js沒有用IssueGroup
// 以下從IssueGroup.scss直接複製貼上的
.issueWrap {
}
.issueBlock, .issueBlock:visited {
	display: block;
	vertical-align: top;
	padding: 20px;
  color: $textColor;
}
.issueBlock:hover {
	background: $lightGrey;
}
@media all and (min-width: 400px){
	.issueWrap {
		padding: 20px 0;
	}
	.issueBlock, .issueBlock:visited {
		display: inline-block;
	}
}



