.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  margin: 0;
}

.about {
  height: 360px;
  background-color: #005060;
  background-image: url("./images/about-background.svg");
  background-position: center center;
  background-size: auto 100%;
  background-repeat: repeat-x;
  margin-bottom: 60px;
  position: relative;
}

.aboutImage {
  display: block;
  height: 100%;
  background-image: url("./images/about-narrow.png");
  background-position: center bottom;
  background-size: 400px;
  background-repeat: no-repeat;
}

.aboutTitle {
  position: absolute;
  top: 15%;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
}

.aboutTitle span {
  display: block;
}

@media all and (max-width: 400px) {
  .aboutImage {
    background-size: contain;
  }
}

@media all and (min-width: 640px) {
  .about {
    height: 300px;
  }
  .aboutImage {
    background-image: url("./images/about.png");
    background-size: contain;
  }
  .aboutTitle {
    display: none;
  }
}

.title {
  text-align: center;
  font-size: 40px;
  width: 100%;
  margin: 70px auto 0 auto;
  display: inline-block;
}

@media all and (min-width: 1400px) {
  .about {
    height: 400px;
  }
  .title {
    margin-top: 100px;
  }
}

.innerTag {
  width: 100%;
  position: absolute;
  top: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
}

.innerTag li {
  display: inline-block;
  vertical-align: middle;
  float: none;
  cursor: pointer;
}

.innerTag a, .innerTag a:visited {
  display: block;
  padding: 0.5em 1em;
  background: #ddd;
  color: #0f0f0f;
  text-decoration: none;
}

.innerTag a:hover, .innerTag a.active {
  background: #0f0f0f;
  color: #f5f5f5;
}

