/* main content */
.content {
  max-width: 560px;
  padding: 40px;
  margin: 0 auto 40px;
  line-height: 1.5;
}

.content h2 {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
}

.content p {
  font-size: 16px;
}

@media all and (min-width: 800px) {
  .content {
    width: 100%;
  }
}

.reminderCard {
  border: 3px solid #0f0f0f;
  background: #fff;
  margin: 40px 0;
  padding: 20px;
}

.reminderCard > :first-child, .reminderCard > :last-child {
  margin: 0;
  text-align: center;
}

/* License */
.licenseImg {
  max-width: 320px;
  width: 100%;
  margin: 0 auto;
  display: block;
  margin-bottom: 50px;
}



