.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  text-align: left;
  position: relative;
  max-width: 600px;
  margin: 0 auto;
}

.controls {
  position: relative;
}

.sortingButtons {
  display: none;
}

@media screen and (min-width: 600px) {
  .controls {
    width: 600px;
  }
  .sortingButtons {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0;
  }
}

.sortingButton {
  display: block;
  width: auto;
  color: #555;
  background: #ddd;
  padding: 10px;
  margin: 0;
  cursor: pointer;
  display: inline-block;
}

.sortingButton:hover, .sortingButtonActive {
  background: #0f0f0f;
  color: #f5f5f5;
}

/*----control-----*/
.controlButton {
  display: block;
  width: auto;
  color: #555;
  background: #ddd;
  padding: 10px;
  margin: 0;
  cursor: pointer;
  display: inline-block;
}

.controlButton:hover, .controlButtonActive {
  background: #0f0f0f;
  color: #f5f5f5;
}

/*----data-----*/
.aRecordRow {
  display: block;
  padding: 20px;
  background: white;
  margin-top: 1px;
}

.positionCube {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  margin: 0 0.5em 0 0;
}

.date, .category {
  display: inline-block;
  vertical-align: middle;
  margin: 0 0.5em;
}

.avatar, .avatar:visited {
  position: absolute;
  display: block;
  overflow: hidden;
  text-align: center;
  text-decoration: none;
  width: 60px;
}

.avatarImg {
  overflow: hidden;
  width: 40px;
  height: 50px;
  margin: 0 auto;
}

.avatarName {
  font-size: 14px;
  line-height: 1.2;
  color: #0f0f0f;
  text-align: center;
}

.avatarName > .name {
  display: inline-block;
  border-bottom: 1px solid transparent;
}

.avatar:hover .avatarName > .name {
  border-bottom-color: #0f0f0f;
}

.detail {
  margin-left: 80px;
}

.content {
  margin-top: 10px;
  line-height: 1.5;
}

.evadingItem {
  margin: 20px;
  text-align: center;
  color: #555;
}

.evadingText {
  font-size: 20px;
  margin: 20px 0;
}

.evadingFAQ {
  margin: 10px 0;
}

