.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

/* ------------------------------------------------ */
/* Typing Animaiton */
/* ------------------------------------------------ */
.storyBlock {
  text-align: left;
  max-width: 600px;
  margin: 0 auto 20px;
  padding: 30px 18px 10px 18px;
  background: #fff;
  box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, 0.05);
  position: relative;
  font-size: 16px;
}

@media all and (min-width: 400px) {
  .storyBlock {
    padding: 30px;
  }
}

@media all and (min-width: 600px) {
  .storyBlock {
    margin: 20px auto 20px auto;
    font-size: 20px;
  }
}

.withMargin {
  margin-top: 30px;
}

/* # of steps = # of characters  */
@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@-moz-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@-webkit-keyframes blink-caret {
  from, to {
    border-color: transparent;
  }
  50% {
    border-color: black;
  }
}

@-moz-keyframes blink-caret {
  from, to {
    border-color: transparent;
  }
  50% {
    border-color: black;
  }
}

.cssTyping {
  font-family: Consolas, monospace;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  position: relative;
}

.animation12 {
  -webkit-animation: typing 400ms steps(12, end);
  -moz-animation: typing 400ms steps(12, end);
}

.animation14 {
  -webkit-animation: typing 500ms steps(14, end);
  -moz-animation: typing 500ms steps(14, end);
}

.animation21 {
  -webkit-animation: typing 1200ms steps(21, end);
  -moz-animation: typing 1200ms steps(21, end);
}

.animation24 {
  -webkit-animation: typing 1450ms steps(24, end);
  -moz-animation: typing 1450ms steps(24, end);
}

.animation26 {
  -webkit-animation: typing 1600ms steps(26, end);
  -moz-animation: typing 1600ms steps(26, end);
}

.cssText {
  display: inline-block;
}

.blinkingCursor {
  vertical-align: top;
  border-right: 2px solid;
  animation: blink-caret 1s step-end infinite;
  -webkit-animation: blink-caret 1s step-end infinite;
  -moz-animation: blink-caret 1s step-end infinite;
}

.arrowSection {
  text-align: right;
  margin-top: 30px;
}

.arrowText {
  display: none;
}

.arrowTextMobile {
  display: inline-block;
  font-size: 15px;
  margin: 0 8px;
  vertical-align: top;
  color: #005060;
}

@media all and (min-width: 600px) {
  .arrowSection {
    position: absolute;
    right: 10px;
    bottom: 10px;
    cursor: pointer;
  }
}

@media all and (min-width: 800px) {
  .arrowText {
    display: inline-block;
    font-size: 15px;
    margin: 0 8px;
    vertical-align: top;
    padding-top: 4px;
    color: #005060;
  }
  .arrowTextMobile {
    display: none;
  }
}

.arrowRight {
  width: 0;
  height: 0;
  display: inline-block;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 16px solid #005060;
}

.battleStrategy {
  color: #005060;
}



