@charset "UTF-8";
.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.articleWrap {
  position: relative;
  max-width: 600px;
  margin: 0 0 150px;
}

.article {
  text-align: left;
  background: #fff;
  padding: 20px;
  margin-top: 100px;
  background: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15);
}

@media all and (min-width: 600px) {
  .articleWrap {
    margin: 0 auto 150px auto;
  }
  .article {
    padding: 40px;
    box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.15);
  }
}

.img {
  position: absolute;
  width: 200px;
  top: -160px;
  left: -10px;
  float: left;
}

.bigTitle {
  font-size: 24px;
  font-weight: bold;
  color: #005060;
  line-height: 1.5;
  text-align: center;
}

.bigSubtitle {
  font-size: 20px;
  color: #555;
  line-height: 1.5;
  text-align: center;
}

.article:after {
  content: '';
  position: absolute;
  top: -32px;
  left: 144px;
  border-bottom: 32px solid #fff;
  border-left: 0 solid transparent;
  border-right: 32px solid transparent;
}

/* 段落 */
.mainContent {
  max-height: 0;
  overflow: hidden;
}

.mainContent.active {
  max-height: 10000px;
}

.mainContent:before {
  content: '';
  display: block;
  margin: 0 auto;
  width: 32px;
  height: 1px;
  background: #aaa;
}

article {
  display: block;
  margin: 0;
  border: none;
  padding: 0;
}

article h1 {
  font-size: 20px;
  font-weight: bold;
  line-height: 1.5;
}

article h2 {
  font-size: 18px;
  font-weight: bold;
  line-height: 1.5;
}

article p {
  margin: 16px 0;
  line-height: 1.5;
}

article section {
  margin: 40px 0;
}

article ul {
  line-height: 1.5;
}

article ul > li {
  margin: 10px;
}

article blockquote {
  background: rgba(180, 174, 163, 0.19);
  margin: 10px 0 10px 40px;
  padding: 20px;
  line-height: 1.6;
}

article blockquote > p {
  margin: 0;
}

article blockquote > p:not(:first-child) {
  margin-top: 16px;
}

article .editorialComment {
  color: #aaa;
}

article .editorialComment:before {
  content: '【編註：';
}

article .editorialComment.editorialComment:after {
  content: '】';
}

article .IssueArticleFigure {
  display: block;
  width: 100%;
}

.customList {
  list-style: none;
  margin: 20px 0;
  padding: 0;
}

.customList > li {
  position: relative;
  margin: 10px 0 0;
  line-height: 1.5;
}

.customList > li:before {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  font-weight: bold;
}

.customList > li.myth {
  margin-left: 64px;
}

.customList > li.myth:before {
  left: -64px;
}

.customList > li.myth:nth-child(1):before {
  content: '迷思一：';
}

.customList > li.myth:nth-child(2):before {
  content: '迷思二：';
}

.customList > li.myth:nth-child(3):before {
  content: '迷思三：';
}

.customList > li.myth:nth-child(4):before {
  content: '迷思四：';
}

.customList > li.myth:nth-child(5):before {
  content: '迷思五：';
}

.customList > li.est {
  margin-left: 96px;
}

.customList > li.est:before {
  left: -96px;
}

.customList > li.est:nth-child(1):before {
  content: '歷史之最一：';
}

.customList > li.est:nth-child(2):before {
  content: '歷史之最二：';
}

.customList > li.est:nth-child(3):before {
  content: '歷史之最三：';
}

.customList > li.est:nth-child(4):before {
  content: '歷史之最四：';
}

.customList > li.est:nth-child(5):before {
  content: '歷史之最五：';
}

.customList > li.partisan {
  margin-left: 32px;
}

.customList > li.partisan:before {
  left: -32px;
}

.customList > li.partisan.ruling:before {
  content: '朝：';
}

.customList > li.partisan.opposition:before {
  content: '野：';
}

.customList > li.partisan.none {
  margin-left: 64px;
}

.customList > li.partisan.none:before {
  left: -64px;
}

.customList > li.partisan.none:before {
  content: '無黨籍：';
}

.customList > li.partyPosition {
  margin-left: 112px;
}

.customList > li.partyPosition:before {
  left: -112px;
}

.customList > li.partyPosition.DPP_aye:before {
  content: '贊成／民進黨：';
}

.customList > li.partyPosition.PFP_aye:before {
  content: '贊成／親民黨：';
}

.customList > li.partyPosition.KMT_unknown:before {
  content: '模糊／國民黨：';
}

.customList > li.partyPosition.KMT_nay:before {
  content: '反對／國民黨：';
}

/* 繼續閱讀 */
.button {
  cursor: pointer;
  width: 104px;
  margin: 0 auto;
  padding: 10px 20px;
  background: #005060;
  color: #f5f5f5;
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.25);
}

.button:hover {
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.5);
}

.peopleLink {
  display: inline-block;
  white-space: nowrap;
  background: rgba(0, 80, 96, 0.15);
  padding: 0 2px;
}



