.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.appbar {
  position: absolute;
  top: 0;
  left: 0;
  background: #f5f5f5;
  padding: 0px;
  height: 76px;
  width: 100%;
  box-shadow: 0 4px 4px 0px rgba(0, 0, 0, 0.25);
  z-index: 99;
}

.appbar.showMenu {
  height: auto;
}

.inner {
  max-width: 1200px;
  position: relative;
  margin: 0 auto;
}

.siteName, .siteName:visited {
  position: absolute;
  display: inline-block;
  margin: 0;
  margin-top: 6px;
  margin-left: 10px;
  text-decoration: none;
}

.siteName img, .siteName:visited img {
  -webkit-filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.5));
  -ms-filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.5));
  filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.5));
}

.siteLogo {
  display: block;
  height: 96px;
  padding: 0;
}

.lists {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
  max-height: 0px;
  overflow: hidden;
  list-style-type: none;
  text-align: left;
}

.lists.showMenu {
  width: 100%;
  max-height: 1000px;
  padding-top: 102px;
}

.lists li {
  display: inline-block;
  margin: 0;
  width: 50%;
  text-align: center;
}

.navItem, .navItem:visited {
  display: block;
  color: #0f0f0f;
  text-decoration: none;
  padding: 11px;
  height: 54px;
}

.navItem:hover, .navItem.active {
  background: rgba(0, 255, 255, 0.25);
}

.navItem > * {
  display: inline-block;
  vertical-align: middle;
}

.rightToggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 54px;
  height: 54px;
  cursor: pointer;
}

.icon {
  display: inline-block;
  margin-right: 3px;
}

@media all and (min-width: 400px) {
  .lists li {
    width: 20%;
  }
  .navItem {
    height: auto;
    padding: 11px 5px;
  }
  .navItem > * {
    display: block;
    margin: 0 auto;
  }
  .navItem > span {
    margin-top: 2px;
  }
}

@media screen and (min-width: 645px) {
  .appbar, .appbar.showMenu {
    height: 76px;
  }
  .lists {
    max-height: 1000px;
    display: block;
    list-style-type: none;
    margin: 0 auto;
    text-align: right;
  }
  .lists li {
    margin: 0;
    width: auto;
    display: inline-block;
  }
  .navItem {
    display: inline-block;
    padding: 11px;
  }
  .rightToggle {
    display: none;
  }
}

@media screen and (min-width: 840px) {
  .appbar, .appbar.showMenu {
    height: 54px;
  }
  .navItem > * {
    display: inline-block;
    margin: 0;
  }
}



