@charset "UTF-8";
.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  position: relative;
}

.coverImg {
  width: 100%;
}

.figCover {
  width: 100%;
}

.figSection {
  margin: 0 auto;
  max-width: 800px;
  padding: 10px 10px 0;
}

.figSection:after {
  content: '';
  display: block;
  position: relative;
  width: 50px;
  height: 1px;
  margin: 75px auto;
  background: #0f0f0f;
}

.chapterItem {
  margin: 8px 0;
}

.chapterItemText {
  cursor: pointer;
  display: inline-block;
  padding: 3px 10px;
  color: #873633;
  font-weight: bold;
}

.chapterItemText:hover {
  background: #873633;
  color: white;
}

.figSection h3 {
  margin-top: 40px;
}

.figSection p {
  line-height: 1.5;
}

.figAuguePoints {
  border-left: 5px solid gray;
  padding-left: 10px;
  line-height: 1.4;
}

.posFigSection {
  max-width: 800px;
  padding: 10px;
  margin: 0 auto;
}

.positionSection {
  display: flex;
  border-top: 1px solid gainsboro;
  border-bottom: 1px solid gainsboro;
}

.legislatorCards {
  flex: 1;
  text-align: center;
  min-height: 180vh;
}

/* 手機版不顯示 */
.recordStream {
  display: none;
}

@media all and (min-width: 730px) {
  .recordStream {
    position: relative;
    background: white;
    padding: 20px;
    display: block;
    overflow: scroll;
    flex: 0 0 460px;
    border-left: 1px solid gainsboro;
  }
  .fixedStream {
    position: fixed;
    top: 0;
    height: 100vh;
    overflow: scroll;
  }
}

/* controls */
.legislatorControls {
  background: white;
  text-align: center;
  border-bottom: 1px solid #D3CFCF;
}

/* 網頁版才有過濾立委的 filter fixed */
@media all and (min-width: 730px) {
  .legislatorCards.fixed {
    padding-top: 60px;
    /* space for the control bar */
    /* control bar is 60px */
  }
  .legislatorControls {
    box-shadow: 0px 2px 2px 0 rgba(0, 0, 0, 0.12);
  }
  .legislatorControls.fixed {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 9;
  }
  .legislatorControls.fixed {
    width: calc(100% - 460px);
  }
}

.selectTitle {
  background: black;
  color: white;
  padding: 5px;
}

.selectBlock {
  display: inline-block;
  padding: 10px;
}

.selectBlock select {
  margin: 0 5px;
  border-radius: 0;
}

.selectBlock select:focus {
  outline: none;
}

.footerSection {
  padding: 100px 40px;
}



