.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  position: relative;
  min-height: 500px;
}

.invisibleBlock {
  max-height: 0px;
  overflow: hidden;
}

.interactiveControl {
  position: fixed;
  top: 86px;
  left: 10px;
  width: 80px;
  background: white;
  padding: 10px;
  z-index: 100;
  cursor: pointer;
  border: 1px solid;
}

.headerNotification {
  background: #005060;
  font-size: 14px;
  color: #f5f5f5;
  text-align: center;
  margin: 0;
  padding: 1em;
  line-height: 1.5;
  will-change: max-height;
}

.headerNotificationText {
  width: 100%;
  margin-bottom: 1em;
  text-align: center;
}

.headerNotification.isHide {
  max-height: 0;
  overflow: hidden;
  transition: all 800ms;
  padding: 0;
}

.undo, .removeButton {
  display: inline-block;
  margin: 0;
  padding: 0.25em 0.5em;
  width: calc(50% - 2px);
  background: rgba(255, 255, 255, 0.5);
  color: #0f0f0f;
  cursor: pointer;
}

.undo:hover, .removeButton:hover {
  background: #fff;
}

.undo {
  margin-right: 4px;
}

@media all and (min-width: 400px) {
  .undo, .removeButton {
    width: auto;
  }
}

@media all and (min-width: 900px) {
  .headerNotificationText {
    display: inline-block;
    width: auto;
    margin: 0 1em 0 0;
  }
}

.settingPanel {
  width: 100%;
  padding-top: 8px;
  background: none;
  font-size: 14px;
}

.settingPanel.withBackground {
  background: #fff;
}

.settingButtons {
  width: 100%;
  text-align: right;
}

.settingButton {
  display: inline-block;
  margin: 0 4px;
  cursor: pointer;
  text-align: center;
}



