.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  background: #005060;
  color: #f5f5f5;
  padding: 20px 40px 55px;
  min-height: 100vh;
}

.innerWrap {
  margin: 0 auto;
  max-width: 1000px;
  margin-top: 10vh;
}

@media all and (min-width: 800px) {
  .innerWrap {
    margin-top: 20vh;
  }
}

.stamp {
  width: 280px;
  height: 180px;
  display: inline-block;
  padding: 10px;
  background: white;
  position: relative;
  -webkit-filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.5));
  /*The stamp cutout will be created using crisp radial gradients*/
  background: radial-gradient(transparent 0px, transparent 4px, white 4px, white);
  /*reducing the gradient size*/
  background-size: 20px 20px;
  /*Offset to move the holes to the edge*/
  background-position: -10px -10px;
}

.stamp:after {
  content: '';
  position: absolute;
  /*We can shrink the pseudo element here to hide the shadow edges*/
  left: 5px;
  top: 5px;
  right: 5px;
  bottom: 5px;
  z-index: -1;
}

/*Some text*/
.stamp:before {
  content: 'WATCHOUT';
  position: absolute;
  bottom: 0;
  left: 0;
  font: bold 24px arial;
  color: #005060;
  opacity: 0.75;
  line-height: 100%;
  padding: 20px;
}

.subscribeImg {
  width: 100%;
}



