@charset "UTF-8";
.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.cardWrap {
  cursor: pointer;
  text-align: center;
  position: relative;
  background: white;
  border: 1px solid white;
  width: 100%;
  margin: 10px 0px;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.12);
}

.cardWrap:last-child {
  margin-bottom: 0;
}

@media all and (min-width: 730px) {
  .cardWrap {
    display: inline-block;
    width: 200px;
    height: 180px;
    margin: 10px;
    vertical-align: top;
    padding-bottom: 0;
    border: 3px solid transparent;
  }
  /* 網頁版 active */
  .cardWrap.active {
    border: 3px solid black;
  }
}

.aboutPosition {
  font-size: 14px;
  text-align: left;
}

.aboutPositionWeb {
  display: none;
}

@media all and (min-width: 730px) {
  .aboutPositionMobile {
    display: none;
  }
  .aboutPositionWeb {
    display: block;
  }
}

.photo {
  text-decoration: none;
  width: 60px;
  height: 60px;
  display: inline-block;
  margin: 10px auto;
  border-radius: 50%;
  background: #fff;
}

.peopleInfo {
  display: inline-block;
  vertical-align: top;
  padding-top: 12px;
  margin-left: 8px;
}

.peopleName {
  margin: 4px;
  font-size: 20px;
  font-weight: bold;
  vertical-align: top;
  max-width: 100px;
}

.partyTitle {
  margin-left: 3px;
  line-height: 1.25;
  display: inline-block;
}

.position {
  padding: 0 10px 0 24px;
}

.posPosition {
  width: 20px;
  height: 20px;
  display: inline-block;
}

.posTitle {
  padding: 0 5px;
  display: inline-block;
  vertical-align: top;
  padding-top: 2px;
}

@media all and (min-width: 730px) {
  .aboutPosition {
    position: absolute;
    bottom: 0;
    width: 100%;
  }
  .position {
    width: 50%;
    display: inline-block;
    padding: 0px;
    text-align: center;
  }
  .position:first-child {
    border-right: 1px solid white;
  }
  .posPosition {
    display: block;
    width: auto;
    height: auto;
    padding: 10px;
  }
  .posTitle {
    display: block;
    background: black;
    color: white;
  }
}

/* 以下只出現在手機版 */
.quoteSwiper {
  display: block;
  padding-bottom: 4px;
  margin-top: 10px;
  width: 100%;
}

@media all and (min-width: 730px) {
  .quoteSwiper {
    display: none;
  }
}

.recordWrap {
  width: 100%;
}

.dotItem {
  display: inline-block;
  width: 10px;
  height: 10px;
  border: 1px solid black;
  border-radius: 50%;
  margin: 2px;
}

.dotItem.active {
  background: black;
}



