@charset "UTF-8";
.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  position: relative;
  max-width: 720px;
  /* 3 castles */
  margin: 0 auto 60px;
  text-align: center;
}

.wrap.embed {
  max-width: 100vw;
  /* embed max width */
}

/* 手機上 safari 需要指定 block */
.coverItem, .coverItem:visited {
  text-decoration: none;
  display: block;
  vertical-align: middle;
  width: 240px;
  margin: 0 auto;
  padding: 0;
  overflow: hidden;
  color: #0f0f0f;
  text-align: center;
  cursor: pointer;
  transition: color 100ms ease-in-out;
}

/* 用在 embed 需要白色背景 */
.coverItem.embed {
  background: rgba(250, 250, 250, 0.4);
  margin: 10px;
  padding: 20px 10px;
}

@media all and (min-width: 480px) {
  .coverItem, .coverItem:visited {
    display: inline-block;
    margin: 0;
  }
}

@media all and (max-width: 239px) {
  .coverItem, .coverItem:visited {
    width: 96%;
    margin: 0 2%;
  }
}

.coverItem:hover .coverImg {
  -webkit-filter: drop-shadow(0 0 15px rgba(0, 255, 255, 0.75));
  -ms-filter: drop-shadow(0 0 15px rgba(0, 255, 255, 0.75));
  filter: drop-shadow(0 0 15px rgba(0, 255, 255, 0.75));
}

.coverImg {
  display: block;
  width: 100%;
}

.coverTitleBlock {
  text-align: center;
  line-height: 24px;
}

.coverTitle {
  color: #005060;
  font-size: 24px;
  margin: 0 2px;
  font-weight: bold;
  display: inline-block;
}

.completedCoverTitle {
  color: #0f0f0f;
}

.coverQuestion {
  line-height: 32px;
}

.missionStatusBlock {
  display: block;
}

.missionStatusBlock > * {
  display: inline-block;
  vertical-align: middle;
}

.missionStatusText {
  color: #005060;
  font-weight: bold;
}

.star {
  transition: transform 0.5s ease-in-out;
}

.coverItem:hover .star {
  -webkit-filter: drop-shadow(0 0 3px gold);
  -ms-filter: drop-shadow(0 0 3px gold);
  filter: drop-shadow(0 0 3px gold);
  transform: rotate(180deg);
}

.comingText {
  text-align: center;
  font-size: 20px;
  color: #aaa;
}



