.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  position: relative;
  background-image: url(./images/hall.jpg);
  background-size: cover;
  min-height: 100vh;
  text-align: center;
}

.embedSection {
  background: rgba(255, 255, 255, 0.4);
  text-align: left;
}

.embedImg {
  width: 300px;
  margin: 10px 0 0 20px;
}

h1.topic {
  margin: 20px auto;
  font-size: 28px;
}

@media all and (min-width: 750px) {
  h1.topic {
    margin: 20px 0 0 10px;
  }
}

.container {
  margin: 20px auto;
  display: inline-block;
}

.flyingImg {
  width: 250px;
  margin: 0 auto;
}

@media all and (min-width: 750px) {
  .flyingImg {
    position: absolute;
    top: 150px;
    left: calc(50% + 160px);
    z-index: 1;
  }
}

.missionImg {
  display: block;
  width: 55%;
  margin: 0 auto;
  padding: 0 10px;
  cursor: pointer;
  -webkit-filter: drop-shadow(0 0 8px aqua);
  -ms-filter: drop-shadow(0 0 8px aqua);
  filter: drop-shadow(0 0 8px aqua);
}

@media all and (min-width: 750px) {
  .missionImg {
    width: 20%;
    margin: 0 auto;
    display: block;
    padding: 0;
    background: none;
    cursor: pointer;
    -webkit-filter: drop-shadow(0 0 15px aqua);
    -ms-filter: drop-shadow(0 0 15px aqua);
    filter: drop-shadow(0 0 15px aqua);
  }
  .missionImg:hover {
    -webkit-filter: drop-shadow(0 0 8px aqua);
    -ms-filter: drop-shadow(0 0 8px aqua);
    filter: drop-shadow(0 0 8px aqua);
  }
}



