@charset "UTF-8";
.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.recordPost {
  text-align: left;
  padding: 20px;
  font-size: 15px;
}

@media all and (min-width: 730px) {
  .recordPost {
    padding: 0 20px;
  }
}

.recordPost.multiple {
  margin-bottom: 8px;
}

.recordMeta {
  margin-bottom: 8px;
}

.quote {
  line-height: 1.5;
  quotes: "“" "”" "‘" "’";
  margin-bottom: 10px;
}

.quote:before, .quote:after {
  display: inline-block;
  color: #ddd;
  font-size: 64px;
  vertical-align: top;
  height: 16px;
  margin-top: -24px;
}

.quote:before {
  content: open-quote;
  margin-right: 2px;
}

.quote:after {
  content: close-quote;
  margin-left: 2px;
}

.recordPosisition {
  border: 1px solid;
  margin-bottom: 10px;
  display: inline-block;
  display: inline-block;
}

.recordPosisitionRow {
  padding: 2px 4px;
}

.recordPosisitionTitle {
  background: black;
  color: white;
}

.recordSource {
  margin: 10px 0 0;
}



