.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  text-align: center;
  position: relative;
}

.main {
  padding: 30px 0;
}

.summary {
  position: relative;
  max-width: 600px;
  min-height: 120px;
  margin: 0 auto;
}

.summary > .positionSquare {
  margin-bottom: 20px;
}

.menuBlock {
  font-size: 14px;
  text-align: right;
  position: absolute;
  right: 0;
  top: 0;
}

@media screen and (max-width: 600px) {
  .menuBlock {
    right: 8px;
  }
}

.menuTitle, .menuTitle:visited {
  cursor: pointer;
  padding: 0 0 4px;
}

.menu {
  padding: 4px 0;
  opacity: 0.75;
}

.menu.menuActive {
  opacity: 1;
}

@media screen and (min-width: 1000px) {
  .summary {
    display: inline-block;
    vertical-align: top;
    margin-right: 20px;
  }
  .menuBlock {
    position: relative;
    text-align: left;
    margin-top: 40px;
  }
  .table {
    display: inline-block;
  }
}

