@charset "UTF-8";
.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.bgWrap {
  width: 100%;
  height: 116vw;
  overflow: hidden;
}

.bgGif {
  width: 100%;
  z-index: -1000;
  overflow: hidden;
  background-size: cover;
  top: 80px;
}

.coverTextBlock {
  position: absolute;
  width: 100%;
  top: 0;
  height: 116vw;
  color: white;
}

@media screen and (min-width: 750px) {
  .bgWrap {
    height: 50vw;
  }
  .bgGif {
    top: 4px;
  }
  .coverTextBlock {
    height: 50vw;
  }
}

.coverTitle {
  position: relative;
  width: 100%;
  margin-top: 45vw;
  /*	text-align: center;
	font-size: 24px;
	font-weight: 800;
	line-height: $textLineHeight;
	text-shadow: 1px 1px 10px #29ABE2;*/
}

.coverTitleMain {
  display: block;
  width: 90%;
  margin: 0 auto;
}

.diffTensImg {
  position: absolute;
  top: 3.6vw;
  width: 5%;
  left: 73.5vw;
}

.diffOnesImg {
  position: absolute;
  top: 3.6vw;
  width: 5%;
  left: 78.5vw;
}

.actions {
  position: absolute;
  bottom: -10.25vw;
  left: 0;
  width: 100%;
}

.storyBeginsHereImg {
  display: block;
  margin: 0 auto;
  width: 85%;
  cursor: pointer;
  -webkit-filter: drop-shadow(0 0 8px #fff);
  -ms-filter: drop-shadow(0 0 8px #fff);
  filter: drop-shadow(0 0 8px #fff);
}

.missionImg {
  display: block;
  width: 55%;
  margin: 10vw auto 0;
  padding: 0 10px;
  cursor: pointer;
  -webkit-filter: drop-shadow(0 0 8px aqua);
  -ms-filter: drop-shadow(0 0 8px aqua);
  filter: drop-shadow(0 0 8px aqua);
}

@media all and (min-width: 750px) {
  .coverTitle {
    margin-top: 17vw;
  }
  .coverTitleMain {
    width: 60%;
  }
  .diffTensImg {
    position: absolute;
    top: 2.5vw;
    width: 3.25%;
    left: 65.75vw;
  }
  .diffOnesImg {
    position: absolute;
    top: 2.5vw;
    width: 3.25vw;
    left: 68.95vw;
  }
  .actions {
    bottom: -4vw;
  }
  .storyBeginsHereImg {
    width: 36%;
  }
  .missionImg {
    width: 20%;
    margin: 4vw auto 0 auto;
    display: block;
    padding: 0;
    background: none;
    cursor: pointer;
    -webkit-filter: drop-shadow(0 0 15px aqua);
    -ms-filter: drop-shadow(0 0 15px aqua);
    filter: drop-shadow(0 0 15px aqua);
  }
  .missionImg:hover {
    -webkit-filter: drop-shadow(0 0 8px aqua);
    -ms-filter: drop-shadow(0 0 8px aqua);
    filter: drop-shadow(0 0 8px aqua);
  }
}

/* 播放影片 */
.playingSection {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
  top: 0;
}

.iframWrap {
  position: fixed;
  width: 100%;
  height: 100%;
}

.close {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 10;
  color: white;
  font-size: 20px;
  cursor: pointer;
  padding: 10px 20px;
  background: black;
}

.close:hover {
  background: #29ABE2;
}

.closeText {
  display: inline-block;
  padding: 0 4px;
}



