.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  position: relative;
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
}

.prevNextWrap {
  width: 100%;
  background: #ddd;
  font-size: 0;
}

.prev, .next {
  display: inline-block;
  width: 50%;
  cursor: pointer;
}

.prev_next {
  display: block;
  height: 30px;
  margin: 7px auto;
}

.next {
  transform: rotate(180deg);
}

@media all and (min-width: 600px) {
  .prevNextWrap {
    position: absolute;
    top: 0;
    background: none;
  }
  .prev, .next {
    width: 50%;
    height: 0;
    padding-bottom: 42.5%;
    opacity: 0.15;
  }
  .prev:hover, .next:hover {
    opacity: 1.0;
  }
  .prev_next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 43px;
    margin: 0;
  }
}

.inactiveSlideImg {
  position: absolute;
  width: 100%;
  opacity: 0;
  top: 0;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.activeSlideImg {
  width: 100%;
  opacity: 1;
}

.slideBlock {
  position: relative;
  top: 0;
  width: 100%;
  display: inline-block;
}

.slideBlock img {
  display: block;
}

.nextPageButton {
  cursor: pointer;
  display: inline-block;
  padding: 10px 20px;
  background: #523F3F;
  border-radius: 3px;
  color: white;
  margin: 10px;
  box-shadow: 0px 4px 0 0 rgba(82, 63, 63, 0.53);
}

.nextPageButton:hover {
  box-shadow: 0px 4px 0 0 transparent;
  margin-top: 14px;
}

.pageWrap {
  position: relative;
}

.page {
  display: inline-block;
  cursor: pointer;
  display: inline-block;
  margin: 8px 4px;
  padding: 0;
  width: 16px;
  height: 16px;
  border-radius: 100%;
  background: #aaa;
}

.page:hover, .activePage {
  color: white;
  background: #0f0f0f;
}

.menuBlock {
  display: inline-block;
  vertical-align: top;
}

.menuItem {
  text-align: left;
  vertical-align: top;
  padding: 10px;
  width: 200px;
  line-height: 1.4;
  background: white;
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.12);
  margin-bottom: 2px;
  margin-right: 8px;
  cursor: pointer;
}

.menuItem.activeMenuItem {
  color: white;
  background: #523F3F;
}



