.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.title {
  font-size: 20px;
  padding: 20px 0;
}

.emailLabel {
  display: none;
}

.red {
  color: #D65A5A;
}

.showAlert {
  display: inline-block;
  color: #D65A5A;
}

.hideAlert {
  display: none;
}

.emailInput {
  display: inline-block;
  height: 40px;
  border: none;
  padding: 5px 10px;
  background: rgba(245, 245, 245, 0.75);
  color: #0f0f0f;
  line-height: 1;
}

.emailInput:focus {
  outline: none;
  background: #f5f5f5;
}

.submit {
  display: block;
  height: 40px;
  margin-top: 10px;
  padding: 0 8px;
  border: none;
  color: #0f0f0f;
  background: rgba(245, 245, 245, 0.75);
}

.submit:hover {
  background: #f5f5f5;
}

@media all and (min-width: 480px) {
  .submit {
    display: inline-block;
    margin: 0 10px;
  }
}

.lawInfo {
  line-height: 1.5;
  font-size: 12px;
}

.bottomInfo {
  display: none;
  line-height: 1.5;
  font-size: 12px;
}

.lawInfo, .lawInfoTitle, .lawInfoContent, .bottomInfo {
  margin: 12px 0;
}

.lawInfo, .bottomInfo {
  opacity: 0.5;
}

@media all and (min-width: 480px) {
  .lawInfo, .bottomInfo {
    width: 50%;
  }
}

.linkItem {
  display: inline-block;
  margin: 10px;
  font-size: 16px;
}

.hiddenLinks {
  max-height: 0px;
  overflow: hidden;
}

.smallIcon {
  font-size: 16px;
  margin: 4px;
}



