@charset "UTF-8";
.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  display: inline-block;
  vertical-align: top;
}

.wrap > * {
  display: block;
  margin: 0 auto;
}

.title {
  padding: 4px 4px 0;
  width: 124px;
  font-size: 20px;
  background: #000;
  color: #fff;
}

.countWrap {
  display: inline-block;
  border: 2px solid #000;
  font-size: 16px;
  line-height: 1;
}

.countTotal {
  display: inline-block;
  width: 120px;
  padding: 0 0 4px;
  background: #000;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
}

.countItem {
  display: inline-block;
  width: 40px;
  padding: 4px 0;
}

.cubesOuterWrap {
  margin-top: 20px;
}

/* 一個一個表態 */
.positionWrap {
  position: relative;
  float: left;
}

.positionCube {
  width: 20px;
  height: 20px;
  float: left;
}

/* hover 上的時候顯示的文字 */
.activeCube {
  width: 100%;
  background: white;
  position: absolute;
  text-align: left;
  padding: 10px;
  font-size: 14px;
  line-height: 1.4;
  bottom: -100px;
  height: 90px;
  width: 340px;
  border: 1px solid;
  overflow: hidden;
  z-index: 1;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.12);
}



