@charset "UTF-8";
.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.wrap {
  display: inline-block;
  margin: 20px 0;
}

@media screen and (min-width: 750px) {
  .wrap {
    margin: 20px;
  }
}

.header {
  display: block;
  max-width: 100vw;
  font-size: 16px;
  margin-bottom: 10px;
  padding: 0 10px;
}

.userPositionBlock {
  position: relative;
  text-align: center;
}

.userPositionText {
  padding: 6px 10px;
  border: 1px solid gray;
  margin: 0 auto 10px auto;
  display: inline-block;
  position: relative;
}

.userPositionText:before {
  content: '';
  border-top: 10px solid #171717;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  position: absolute;
  bottom: -10px;
  left: calc(50% - 5px);
}

.userPositionText:after {
  content: '';
  border-top: 18px solid #eeeeee;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  position: absolute;
  bottom: -9px;
  left: calc(50% - 9px);
}

.svgWrap {
  vertical-align: middle;
}

.rectWrap {
  display: none;
}

@media screen and (min-width: 750px) {
  .rectWrap {
    display: inline-block;
  }
}

.partyTitle {
  font-size: 22px;
  display: inline-block;
}

.metaTitle {
  color: #555;
  font-size: 14px;
  line-height: 1.4;
}

.positionWrap {
  position: relative;
  float: left;
}

.positionCube, .positionCube:visited {
  width: 20px;
  height: 20px;
  float: left;
  position: relative;
  cursor: pointer;
  color: black;
  text-decoration: none;
}

.positionCube:hover, .positionCubeActive {
  box-shadow: 0 0 0 2px black;
  z-index: 10;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

/* hover 上的時候顯示的文字 */
.activeBlock {
  position: absolute;
  top: -160px;
  z-index: 100;
}

.activeCube, .activeCube:visited {
  width: 100%;
  background: white;
  text-align: left;
  padding: 10px;
  font-size: 14px;
  line-height: 1.4;
  width: 340px;
  border: 1px solid;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.12);
  cursor: pointer;
  display: block;
  color: black;
  text-decoration: none;
}

.activeContent {
  height: 100px;
  overflow: hidden;
  margin: 5px 0;
}

.caucus {
  background-image: url("./images/command-2_black.svg");
  background-size: cover;
}



