.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.none {
  background: gray;
}

.partyFlag {
  display: inline-block;
  vertical-align: top;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.partyFlag.small {
  width: 22px;
  height: 22px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background-image: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background-image: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background-image: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background-image: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background-image: url(images/flag-none.svg);
}

.partyFlag.NP {
  background-image: url(images/flag-np.svg);
}

.partyFlag.NPP {
  background-image: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background-image: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background-image: url(images/flag-tsu.svg);
}

.partyFlag.NSU {
  background-image: url(images/flag-nsu.svg);
}

.home {
  position: relative;
}

.invisible {
  max-height: 0;
  overflow: hidden;
}

.contentWrap {
  padding: 60px 0 0;
  border-top: 1px solid #e8f6fb;
}

.innerWrap {
  max-width: 1200px;
  margin: 0 auto;
}

.alignCenter {
  text-align: center;
}

.coverItem, .coverItem:visited {
  display: inline-block;
  margin: 0 auto;
  font-size: 20px;
  font-weight: bold;
  margin: 0 auto 50px auto;
  text-decoration: none;
}

.coverItem img {
  width: 260px;
}

.coverItem *, .coverItem:visited * {
  text-decoration: none;
  color: black;
}

.coverImg {
  cursor: pointer;
}

@-webkit-keyframes toChina {
  from {
    -webkit-filter: drop-shadow(0 0 15px rgba(255, 222, 0, 0.75));
    -ms-filter: drop-shadow(0 0 15px rgba(255, 222, 0, 0.75));
    filter: drop-shadow(0 0 15px rgba(255, 222, 0, 0.75));
  }
  to {
    -webkit-filter: drop-shadow(0 0 15px rgba(222, 41, 16, 0.75));
    -ms-filter: drop-shadow(0 0 15px rgba(222, 41, 16, 0.75));
    filter: drop-shadow(0 0 15px rgba(222, 41, 16, 0.75));
  }
}

.coverItem:hover .coverImg {
  animation: toChina 4s;
  -webkit-animation-fill-mode: forwards;
}

.pageTitle {
  text-align: center;
  font-size: 26px;
  padding: 15px 0;
  margin: 35px 0 15px 0;
  color: #0f0f0f;
}

.roadmap {
  width: 100%;
  text-align: center;
  background: rgba(0, 80, 96, 0.15);
  box-shadow: 0 -4px 4px rgba(0, 0, 0, 0.15);
}

.roadmap > .stage {
  display: inline-block;
  vertical-align: top;
  height: 240px;
  width: 320px;
  margin: 0 20px;
  background-image: url("./images/roadmap-01.png");
  background-position: 0 0;
  background-size: auto 100%;
}

.roadmap > .stage.october, .roadmap > .stage.november {
  height: 200px;
  background-size: auto 240px;
}

.roadmap > .stage.october {
  width: 222px;
  background-position: -34px 0;
}

.roadmap > .stage.november {
  width: 177px;
  background-position: -283px 0;
}

.roadmap > .stage.december {
  width: 247px;
  background-position: -478px 0;
}

.roadmap > .stage.january {
  width: 231px;
  background-position: -746px 0;
}



